/* eslint-disable @typescript-eslint/ban-types */


/**
 * @apiManager is a simple litteral to expose a few methods globally.
 * This is to allow other scripts to simply communicate with the wireframe.
 * For example the pimcore plugin, to add blocks but also to retrieve styles
 */

type apiManagerType = {
  /** This object is exposed on global Window  */
  exposedFunction: { [key: string]: Function };

  /** Use this method for add A method in ActiveWireframe Api
   * @param fn - The function added
   * @param key - The name to call this function,
   * Call example : window.ActiveWireframe.Yourfunction()
   */
  addFunction(fn: Function, key: string): void;
};

const apiManager: apiManagerType = {
  exposedFunction: {},

  addFunction(fn: Function, key: string): void {
    this.exposedFunction[key] = fn;
  },
};

window.ActiveWireframe = apiManager.exposedFunction;

export default apiManager;
declare global {
  interface Window {
    ActiveWireframe: object;
  }
}
